import {Component, OnInit} from '@angular/core';
import * as moment from 'moment';
import {InterfaceService} from './interface.service';
import {RequestInterface} from './interace';

declare var $;

@Component({
  selector: 'ngx-demo',
  templateUrl: './interface.component.html',
  styleUrls: ['./interface.component.scss'],
})

export class InterfaceComponent {
  title: string = '';
  selected: string = '';
  interfaces: string[] = [
    'APP', '', 'APP', '', '', 'APP', '', '',
  ];

  times: string[] = [
    '', '', '',
  ];

  constructor(private interfaceService: InterfaceService) {
    interfaceService.getTitleSubject().subscribe(title => this.title = title);
  }

  handleTime(s: string): void {
    console.log(s);
    const requestInterface = new RequestInterface();
    /* requestInterface.endTime = moment().subtract(1, 'days').format('YYYY-MM-DD');*/
    requestInterface.endTime = '2018-04-06';
    if (s === '') {
      requestInterface.type = 1;
    } else if (s === '') {
      requestInterface.type = 2;
    } else if (s === '') {
      requestInterface.type = 3;
    }
    console.log(requestInterface);
    this.interfaceService.changeTime(requestInterface);
  }
}



